import { Link } from "gatsby"
import React from "react"

import { useTranslation } from "react-i18next"

import LanguagSwitcher from "./Switcher"

const MENU_LINKS = [
  {
    text: "Home",
    to: "/",
  },
  {
    text: "About",
    to: "/about",
  },
  {
    text: "Projects",
    to: "/projects",
  },
]

const Header = ({ toggle }: { toggle: () => void }) => {
  const { t } = useTranslation()
  return (
    <>
      <header className="cd-header px-2 md:px-0" onBlur={() => toggle}>
        <div className="header-wrapper">
          <div className="nav-but-wrap">
            <div className="menu-icon hover-target" onClick={toggle}>
              <span className="menu-icon__line menu-icon__line-left"></span>
              <span className="menu-icon__line"></span>
              <span className="menu-icon__line menu-icon__line-right"></span>
            </div>
          </div>
        </div>
        <LanguagSwitcher />
      </header>

      <div className="nav">
        <div className="nav__content">
          <ul className="nav__list">
            {MENU_LINKS.map(menu => (
              <li className="nav__list-item" key={menu.text}>
                <Link
                  to={menu.to}
                  className="hover-target"
                  activeClassName="active-nav"
                >
                  {t(menu.text)}
                </Link>
              </li>
            ))}
          </ul>
        </div>
      </div>
    </>
  )
}

export default Header
